*, ::after, ::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: 100%;
}

i {
  width: 2rem;
  margin-right: 2rem;
}

.contenido {
  position: absolute;
  width: calc(100% - 8rem);
  min-height: calc(100vh - 8rem);
  top: 8rem;
  left: 8rem;
  transition: all 0.3s ease-in-out;
  padding-top: 5rem;
}
.contenido.redimensionar {
  left: 25rem;
  width: calc(100% - 25rem);
}
@media (max-width: 768px) {
  .contenido {
    left: 0;
    width: 100%;
  }
}
@media (max-width: 480px) {
  .contenido.redimensionar {
    left: 0;
    width: 100%;
  }
}
.contenido #cont-paginas {
  height: 100%;
}
.contenido #cont-paginas .dinamico {
  width: 100%;
  height: 100%;
  border: none;
}

.header {
  width: 100%;
  height: 8rem;
  background: #f5f5f5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 20;
  transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
}
.header .icon__menu {
  width: 8rem;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 1rem;
}
.header .icon__menu i {
  font-size: 2rem;
  cursor: pointer;
  position: absolute;
  color: #3b5895;
}
.header h1 {
  color: #3b5895;
}
.header .logo {
  width: 15rem;
  height: 6rem;
  margin-right: 3rem;
}

.menu__side {
  width: 8rem;
  height: 100%;
  background: #3b5895;
  position: fixed;
  top: 8rem;
  left: 0;
  font-size: 1.8rem;
  z-index: 21;
  overflow: hidden;
  overflow-y: scroll;
  border-right: 2rem solid #3b5895;
  transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
  box-shadow: 14px -1px 4px -11px rgba(0, 0, 0, 0.59);
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.menu__side .name__page {
  color: #ffffff;
  padding: 2rem 3rem;
  display: flex;
  align-items: center;
}
.menu__side::-webkit-scrollbar {
  display: none;
}
.menu__side.side__move {
  width: 25rem;
}
@media (max-width: 768px) {
  .menu__side {
    left: -25rem;
    width: 25rem;
  }
  .menu__side.side__move {
    left: 0rem;
  }
}

.barra__progreso {
  padding: 2rem 3rem;
}
.barra__progreso .barra {
  width: 100%;
  height: 0.3rem;
  background: #ffffffb2;
  margin-bottom: 1rem;
  position: relative;
}
.barra__progreso .barra::before {
  content: "";
  position: absolute;
  background: #ffffff;
  width: 75%;
  height: 100%;
  top: 0;
  left: 0;
}
.barra__progreso #progreso__mensaje {
  font-size: 2rem;
  color: #ffffff;
}

.options__menu {
  padding: 2rem 3rem;
  position: absolute;
  top: 18rem;
}
.options__menu a {
  color: #ffffffb2;
  cursor: default;
  display: block;
  position: relative;
  transition: all 0.3s ease;
}
.options__menu a:hover {
  color: #ffffff;
}
.options__menu a.select {
  color: #ffffff;
}
.options__menu a.select::before {
  content: "";
  width: 0.3rem;
  height: 80%;
  position: absolute;
  top: 10%;
  left: -3rem;
  background: #ffffff;
}
.options__menu a .option {
  padding: 2rem 0;
  display: flex;
  align-items: center;
  position: relative;
}
.options__menu a .option i {
  cursor: pointer;
}
.options__menu a .option h4 {
  font-weight: 300;
  cursor: pointer;
}

.btn_nav {
  position: fixed;
  width: 30rem;
  height: 6rem;
  right: 10rem;
  bottom: 0;
  left: 40%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background: rgba(61, 137, 199, 0.5);
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
}
@media (max-width: 768px) {
  .btn_nav {
    left: 20%;
  }
}
@media (max-width: 480px) {
  .btn_nav {
    left: 10%;
  }
}
.btn_nav i {
  width: auto;
  margin: 0;
  font-size: 4.2rem;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.2s ease-in;
}
.btn_nav i:hover {
  color: #3b5895;
}

.contenedor #titulo_01 {
  color: #2c4268;
  text-align: center;
}
.contenedor #parrafo_01 {
  font-size: 2rem;
  color: #ea6174;
  text-align: justify;
  margin-bottom: 2rem;
}
.contenedor .cont_img01 {
  width: 30rem;
}

#intro_actividad {
  width: 100%;
  height: 60rem;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row-reverse;
  background: #129a63;
}
#intro_actividad #vineta {
  width: 60rem;
  background: #ffffff;
  padding: 2.4rem;
  border-radius: 2rem;
  border: 5px solid #3b5895;
  font-size: 2.4rem;
  text-align: center;
  position: absolute;
  top: 6rem;
  left: 20rem;
}
#intro_actividad #vineta #relativo {
  position: relative;
}
#intro_actividad #vineta #relativo #frase_01 {
  color: #3b5895;
  font-weight: 700;
}
#intro_actividad #vineta #relativo #frase_02 {
  color: #129a63;
  font-weight: 300;
}
#intro_actividad #vineta #relativo::after {
  content: "";
  position: absolute;
  background: #3b5895;
  top: 50%;
  right: -5.6rem;
  width: 3rem;
  height: 4rem;
  clip-path: polygon(0 0, 0% 100%, 100% 50%);
}
#intro_actividad .cont-imagen {
  width: 40%;
  height: 100%;
  padding: 2rem 2rem 0 2rem;
  background: #616a84;
}